import React, { Component } from 'react';
import { Provider } from "react-redux";
import Sidebar from "react-sidebar";
import { Route, Switch } from "react-router";
import { ConnectedRouter } from "connected-react-router";
import { PersistGate } from "redux-persist/integration/react";
import 'bootstrap/dist/css/bootstrap.min.css';

import ErrorBoundary from './ErrorBoundary.js'
import routes from "./router-config";
import history from "./history";
import store from "./store";
import SidebarContent from "./components/sidebar";
import Header from "./containers/HeaderContainer";

import './global';
import './styles/core.scss';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sidebarOpen: false,
    };
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
  }

  componentDidMount() {
    const token = window.cookieHandler.get('token');
    if(!token && window.location.pathname !== "/") window.location.href = "/";
  }

  onSetSidebarOpen() {
    const {sidebarOpen} = this.state;
    this.setState({ sidebarOpen: !sidebarOpen });
  }

  render() {
    const { sidebarOpen } = this.state;
    const sidebar = <SidebarContent onSetSidebarOpen={this.onSetSidebarOpen} />;
    return (
      <Provider store={store.store}>
        <PersistGate loading={null} persistor={store.persistor}>
          <ConnectedRouter history={history}>
            <div className="app-container">
              <Sidebar
                sidebar={sidebar}
                open={sidebarOpen}
                onSetOpen={this.onSetSidebarOpen}
              >
                <Header onSetSidebarOpen={this.onSetSidebarOpen} />
                <div className="view-container">
                  <Switch>
                    {routes.map((route, index) => (
                      <Route
                        key={index}
                        path={route.path}
                        component={route.component}
                        exact={route.exact}
                      />
                    ))}
                  </Switch>
                </div>
              </Sidebar>
            </div>
          </ConnectedRouter>
        </PersistGate>
      </Provider>
    )
  }
}

export default App;
